  };\r\n  },\r\n  computed: {\r\n    defaultSeatClass: {\r\n      get: function () {\r\n        return this.defaultSeatClassId;\r\n      },\r\n      set: function (value) {\r\n        this.defaultSeatClassId = value;\r\n      },\r\n    },\r\n    seatTooltip() {\r\n      return this.seat.disabled\r\n        ? `Disabled seat`\r\n        : `${this.seat.class.name} class`;\r\n    },\r\n    seatClassColors() {\r\n      let data = {};\r\n      if (this.seat.disabled) {\r\n        data = {\r\n          \"--disabled-stroke\": \"#ada9a9\",\r\n          \"--disabled-fill\": \"#ada9a9\",\r\n        };\r\n      } else if (this.seat.class) {\r\n        data = {\r\n          \"--stroke\": \"transparent\",\r\n          \"--fill\": this.seat.class.color,\r\n        };\r\n      }\r\n\r\n      return data;\r\n    },\r\n  },\r\n  watch: {\r\n    // seat() {\r\n    //   this.defaultSeatClass = this.seat.class.id;\r\n    // }\r\n  },\r\n  methods: {\r\n    seatCentre(r, c) {\r\n      let seat = this.getSeatWithRC(r, c);\r\n      if (seat != null) {\r\n        if (seat.disabled) {\r\n          return \"cls-ra cls-disabled \";\r\n        }\r\n        return \"cls-fa\";\r\n      }\r\n    },\r\n    classifier(r, c) {\r\n      let seat = this.getSeatWithRC(r, c);\r\n      if (seat != null) {\r\n        if (seat.disabled) {\r\n          return \"cls-ra cls-disabled \";\r\n        }\r\n\r\n        return \"cls-ra\";\r\n      }\r\n    },\r\n    makeSeatAGap() {\r\n      this.makeGap(this.seat);\r\n    },\r\n    disableMySeat() {\r\n      this.disableSeat({\r\n        row: this.seat.position.r,\r\n        col: this.seat.position.c,\r\n      });\r\n    },\r\n    renameMySeat() {\r\n      var seatNumber = prompt(\"Please enter your name\", this.seat.seat_number);\r\n      if (seatNumber) {\r\n        if (!this.getSeat(seatNumber)) {\r\n          this.renameSeat({\r\n            row: this.seat.position.r,\r\n            col: this.seat.position.c,\r\n            seat_number: seatNumber,\r\n          });\r\n        } else if (seatNumber != this.seat.seat_number) {\r\n          alert(\"seat number is already taken\");\r\n        }\r\n      }\r\n    },\r\n    showModal() {\r\n      this.open = true;\r\n    },\r\n    closeModal() {\r\n      this.open = false;\r\n    },\r\n    changeClass() {\r\n      this.changeSeatClass(this.seat.seat_number, this.defaultSeatClass);\r\n      this.closeModal();\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.ml-2 {\r\n  margin-left: 8px;\r\n}\r\n.ml-auto {\r\n  margin-left: auto;\r\n}\r\n.text-right {\r\n  text-align: right;\r\n}\r\n\r\n.modal {\r\n  width: 450px;\r\n  box-sizing: border-box;\r\n  background-color: #fff;\r\n  font-size: 20px;\r\n  text-align: center;\r\n  display: block;\r\n  position: initial;\r\n}\r\n.seat {\r\n  -webkit-transform: rotate(90deg);\r\n  -moz-transform: rotate(90deg);\r\n  -o-transform: rotate(90deg);\r\n  -ms-transform: rotate(90deg);\r\n  transform: rotate(90deg);\r\n  cursor: pointer;\r\n}\r\n\r\n.seat.disabled {\r\n  cursor: default;\r\n  /* pointer-events:none; */\r\n}\r\n.seat-label {\r\n  font-size: 23px;\r\n  font-weight: 700;\r\n  color: #333;\r\n}\r\n.cls-selected {\r\n  fill: green;\r\n  stroke: #000;\r\n  stroke-miterlimit: 10;\r\n}\r\n.cls-disabled {\r\n  fill: var(--disabled-fill) !important;\r\n  stroke: var(--disabled-stroke) !important;\r\n  stroke-miterlimit: 10;\r\n}\r\n.cls-ra:not(.cls-disabled) {\r\n  /* fill: #fff;\r\n  stroke: rgb(7, 7, 7); */\r\n  fill: var(--fill) !important;\r\n  stroke: var(--stroke) !important;\r\n  stroke-miterlimit: 10;\r\n  stroke-width: 1;\r\n}\r\n.cls-rb {\r\n  fill: gray;\r\n  stroke: #000;\r\n  stroke-miterlimit: 10;\r\n}\r\n.cls-fa {\r\n  fill: #fff;\r\n  stroke-miterlimit: 10;\r\n}\r\n.cls-fb {\r\n  fill: pink;\r\n  stroke: red;\r\n  stroke-miterlimit: 10;\r\n}\r\n.cls-ma {\r\n  fill: #fff;\r\n  stroke: blue;\r\n  stroke-miterlimit: 10;\r\n}\r\n.cls-mb {\r\n  fill: royalblue;\r\n  stroke: blue;\r\n  stroke-miterlimit: 10;\r\n}\r\n